.footer__logo {
  margin-right: 40px; }

@media (max-width: 767px) {
  .footer {
    padding-top: 40px;
    padding-bottom: 40px; }
  .footer__wrapper {
    flex-direction: column;
    align-items: start; }
  .footer__logo {
    margin-bottom: 30px; }
  .footer__nav {
    margin-bottom: 30px; }
  .footer__social {
    margin: 0; } }

.social__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center; }

.social__item {
  margin-right: 33px; }
  .social__item:hover {
    opacity: 0.7; }
  .social__item:active {
    opacity: 0.3; }
  .social__item:last-child {
    margin-right: 0; }

@media (max-width: 1023px) {
  .social__fb svg {
    width: 9px;
    height: 16px; }
  .social__insta svg {
    width: 16px;
    height: 16px; }
  .social__twit svg {
    width: 16px;
    height: 13px; }
  .social__item {
    margin-right: 23px; }
    .social__item:last-child {
      margin-right: 0; } }

.visually-hidden {
  position: absolute;
  visibility: hidden;
  pointer-events: none;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  text-indent: -9999px;
  white-space: nowrap;
  overflow: hidden; }
